import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      redirect: "/login"
    },
    {
      path: '/home',
      name: '登录',
      component: () => import('@/views/Login/index')
    },
    {
      path: '/details',
      name: '商品详情',
      component: () => import('@/views/Details')
    },
    {
      path: '/RetrievePass',
      name: '找回密码',

      component: () => import('@/views/RetrievePass')
    },
    {
      path: '/agree',
      name: '协议',

      component: () => import('@/views/Agreement/index')
    },
    {
      path: '/404',
      name: '404',
      component: () => import('@/views/404/index')
    },
    {
      path: '/home',
      name: '首页',
      component: () => import('@/views/Home/index')
    },
    {
      path: '/classify',
      name: '分类',
      component: () => import('@/views/Classify/index')
    },
    {
      path: '/car',
      name: '购物车',
      component: () => import('@/views/Car/index')
    },
    {
      path: '/my',
      name: '我的',
      meta: { requiresAuth: true }, // 添加表示需要验证
      component: () => import('@/views/My/index')
    },{
      path: '/Enterprise',
      name: '企业入驻',
      meta: { requiresAuth: false }, // 添加表示需要验证
      component: () => import('@/views/Enterprise/index')
    },
    {
      path: '/search',
      name: '搜索',
      component: () => import('@/views/Search/index')
    }

  ]
})



