export default {
  name: "index",
  data() {
    return {
      toolbars:[
        {
          id:0,
          icon1:require('../images/home_01.png'),
          icon2:require('../images/home_02.png'),
          url:'/home',
          isActive:false,
          text:'市场'
        },
        {
          id:1,
          icon1:require('../images/classify01.png'),
          icon2:require('../images/classify02.png'),
          url:'/classify',
          isActive:false,
          text:'分类'
        },
        {
          id:2,
          icon1:require('../images/car01.png'),
          icon2:require('../images/car02.png'),
          url:'/car',
          isActive:false,
          text:'购物车'
        },
        {
          id:3,
          icon1:require('../images/my01.png'),
          icon2:require('../images/my02.png'),
          url:'/my',
          isActive:false,
          text:'我的'
        }
      ]
    }
  },
  methods: {
    handleClick(index){
      for(let i in this.toolbars) {
        this.toolbars[i].isActive = false;
      }
      this.toolbars[index].isActive = !this.toolbars[index].isActive;
    },
    lock(e){
      //锁定tobar
      for (let i of e) {
        if (i.url === this.$route.path) {
          i.isActive = true
        } else {
          i.isActive = false
        }
      }
    },
  },
  watch: {
    $route(e){
      this.lock(this.toolbars)
    }
  },
  mounted() {
    this.lock(this.toolbars)
  }
}



